
import XHeader from 'vux/src/components/x-header/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex'
import api from '../../fetch/api'
export default {
    components: {
        XHeader,
        XImg,
        dateFormat,
        Toast
    },
    created() {
        this.$store.dispatch('get_circles_detail', {id: this.$route.query.circleid,uid: localStorage.getItem('loginopenid')})
        this.$store.dispatch('get_circles_yz_list', {begin: 0,offset: 100,cid: this.$route.query.circleid,uid: localStorage.getItem('loginopenid')})
    },
    computed: {
        ...mapGetters([
            'circles_detail',
            'circles_yz_list'
        ])
    },
    data() {
        return {
            show_success: false,
        }
    },
    methods: {
        //图片加载成功和失败
        success(src, ele) {
            const span = ele.parentNode.querySelector('span')
            ele.parentNode.removeChild(span)
        },
        error(src, ele, msg) {
            const span = ele.parentNode.querySelector('span')
            span.innerText = '加载失败。。。'
        },
        _add_circle() {
            let data = {
                uid: localStorage.getItem('loginopenid'),
                cid: this.$route.query.circleid
            }
            api.v3_circle_join(data).then(res => {
                console.log(res)
                if (res.retcode == 200) {
                    this.show_success = true;
                }
            }).catch(error => {
                console.log(error)
            })
        }
    },
    filters: {
        filterLoc: function (val) {
            return val.toString();
        },
        filterComment: function (val) {
            let arr = [];
            for (let i in val) {
                arr.push(i);
            }
            return arr.length;
        },
        filterDate: function (val) {
            if ((new Date().getTime() - new Date(val.replace(/-/, "/"))) / 1000 < 60) {
                return "1分钟前"
            } else if ((new Date().getTime() - new Date(val.replace(/-/, "/"))) / 1000 < 600) {
                return "5分钟前"
            } else if ((new Date().getTime() - new Date(val.replace(/-/, "/"))) / 1000 < 900) {
                return "10分钟前"
            } else if ((new Date().getTime() - new Date(val.replace(/-/, "/"))) / 1000 < 5000) {
                return "1小时前"
            } else if ((new Date().getTime() - new Date(val.replace(/-/, "/"))) / 1000 < 10000) {
                return "2小时前"
            } else if ((new Date().getTime() - new Date(val.replace(/-/, "/"))) / 1000 < 100000) {
                return "1天前"
            } else if ((new Date().getTime() - new Date(val.replace(/-/, "/"))) / 1000 < 200000) {
                return "2天前"
            } else {
                return dateFormat(val, 'MM-DD HH:mm')
            }

        }
    },
}




